@import "_import";

.l-frame__content{
  height: 100%;
  width: 100%;
  overflow: hidden;
}
.l-sec--max-full{
  position: relative;
  padding: 0 80rem;
  @include full{
    padding: 0 80px;
  }
  @include mobile{
    padding: 0 16rem;
  }
  @include tablet{
    padding: 0 26rem;
  }
}
.br-only-pc{
  display: block !important;
  visibility: visible !important;
  @include mobile{
    display: none !important;
    visibility: hidden !important;
  }
}
.br-only-mo{
    display: none;
    visibility: hidden;
  @include mobile{
    display: block;
    visibility: visible;
  }
}
.c-dimmed{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100vh;
  background-color: #000;
  opacity: 0;
  @include mobile{
    height: -webkit-fill-available;
  }
 }
 .flex-wrap{
  display: flex;
  flex-direction: row;
  width: 100%;
  &__left{
    width: 50%;
  }
  &__right{
    width: 50%;
  }
}
.section{
  position: relative;
  width: 100%;
  background: #fff;
  box-shadow: 0px 0px 30px 10px rgba(126, 83, 83, 0.1);
  @at-root .br-ie &{
    box-shadow: 0px 0px 60px 10px rgba(0, 0, 0, 0.1);
  }
  @include mobile{
    // &.pinning{
    //   top: auto !important;
    //   bottom: 0 !important;
    // }
  }
}
.section01{
  height: 100vh;
  z-index: 1;
  @include mobile{
    // height: 100vh;
    // height: -webkit-fill-available;
    &.pinning{
      top: 0 !important;
    }
  }
}
.section02{
  height: auto;
  z-index: 2;
  @include mobile{
    &.pinning{
      top: 0 !important;
    }
  }
}
.section03{
  height: auto;
  z-index: 3;
}
.section04{
  height: auto;
  z-index: 4;
}
.section05{
  height: auto;
  z-index: 5;
}
.point-txt{
  font-family: "roslindaletext";
}
.visual{
  width: 100%;
  height: 100vh;
  overflow: hidden;
  z-index: 1;
  &__motion-wrap{
    position: relative;
    width: 100%;
    height: 100%;
  }
  &__img{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    overflow: hidden;
    visibility: hidden;
    &-01{
      width: 37.5vw;
      max-width: 720px;
      @include mobile{
        width: 78.66vw;
      }
    }
    &-02{
      width: 25vw;
      max-width: 480px;
      @include mobile{
        width: 52.53vw;
      }
    }
    &-03{
      width: 56.25vw;
      max-width: 1080px;
      @include mobile{
        width: 100vw;
      }
    }
    &-04{
      width: 37.5%;
      height: 100vh;
      // @include mobile{
      //   height: 100vh;
      //   height: -webkit-fill-available;
      // }
      img{
        position: absolute;
        left: 50%;
        top: 0;
        // transform: translate(-50%, 0) scale(1.2);
        transform-origin: 50% 0%;
        width: auto;
        min-width: 100%;
        min-height: 100vh;
        @include mobile{
          // width: 100%;
        }
        // @include mobile{
        //   min-height: 100vh;
        //   min-height: -webkit-fill-available;
        // }
      }
      .visual-mo{
        display: none
      }
      @include mobile{
        width: 78.66vw;
        height: 118.133vw;
        .visual-pc{
          display: none
        }
        .visual-mo{
          display: block
        }
        img{
          // transform: translate(-50%, 0%) scale(1.2);
          transform-origin: 50% 50%;
        }
      }
    }
  }
  &__tit{
    position: absolute;
    bottom: 0%;
    left: 0;
    width: 100%;
    text-align: center;
    font-family: "ambit-semi";
    font-size: 120rem;
    line-height: 132rem;
    white-space: nowrap;
    color: #000;
    .point-txt{
      font-size: 120rem;
      @include mobile{
        font-size: 44rem;
      }
    }
    @include text-effect('.txt-target');
    @include mobile{
      font-size: 44rem;
    }
    // @include tablet{
    //   font-size: 78rem;
    // }
    .br-ie &{
      vertical-align: text-bottom;
    }
  }
  &__desc{
    position: absolute;
    bottom: 50%;
    left: 50%;
    transform: translate(-50%,50%);
    padding-top: 330rem;
    font-family: "Noto Sans KR Medium";
    font-size: 28rem;
    line-height: 44rem;
    text-align: center;
    color: #FFF;
    @include text-effect('.txt-target');
    span{
      display:block;
    }
    @include mobile{
      bottom: 50%;
      width: 100%;
      transform: translate(-50%,150%);
      font-size: 18rem;
      line-height: 26rem;
      padding-top: 0;
    }
    @include tablet{
      bottom: 50%;
      transform: translate(-50%,150%);
    }
  }
}


